@import '../settings/colors';
@import '../settings/globals';

$template-lede-margin: 0 !default;
$template-lede-padding: 70px !default;
$template-lede-to-article-title: 70px !default;

.dvz-content {
  .lede {
    margin: $template-lede-margin;
    padding-top: $template-lede-padding;

    div.dvz-social-media-buttons { // sass-lint:disable-line force-element-nesting no-qualifying-elements
      padding-top: 28px;
      padding-bottom: 0;
    }
  }

  .lede-image ~ .article-title,
  .lede-caption ~ .article-title {
    margin-top: $template-lede-to-article-title;
  }

  .container-width {
    margin-right: auto;
    margin-left: auto;
    max-width: $container-width;
  }

  .copy-width {
    margin-right: auto;
    margin-left: auto;
    max-width: $copy-width;
  }

  .img-full {
    display: block;
    width: 100%;
  }

  @media only screen and (max-width: $container-breakpoint) {
    .container-width {
      max-width: $responsive-max-width;
    }
  }

  @media only screen and (max-width: $copy-breakpoint) {
    .copy-width {
      max-width: $responsive-max-width;
    }

    .container-width,
    .ai2html-width,
    .copy-width {
      .copy-width {
        max-width: 100%;
      }
    }
  }
}
