@import '../tools/functions';

////////////////////////////////////////
//|
//| Sizes
//|
////////////////////////////////////////
$responsive-max-width: 92% !default;

$container-width: 1160px !default;
$container-breakpoint: $container-width / strip-unit($responsive-max-width / 100);

$copy-width: 740px !default;
$copy-breakpoint: $copy-width / strip-unit($responsive-max-width / 100);

$b-tablet-breakpoint: 1020px !default;
$b-mobile-breakpoint: 760px !default;
$b-tiny-breakpoint: 375px !default;

////////////////////////////////////////
//|
//| Fonts
//|
////////////////////////////////////////
$tiempos-text: ('BWHaasDingbat', 'TiemposText', 'Georgia', 'Cambria', 'Times New Roman', 'Times', serif);
$tiempos-head: ('BWHaasDingbat', 'TiemposHead', 'Georgia', 'Cambria', 'Times New Roman', 'Times', serif);

$publico-text: ('BWHaasDingbat', 'Publico Text Web', Georgia, Cambria, 'Times New Roman', Times, serif);
$publico-head: ('BWHaasDingbat', 'Publico Headline Web', Georgia, Cambria, 'Times New Roman', Times, serif);

$publico-mono: ('BWHaasDingbat', 'Publico Text Mono', 'Publico Text Web', Georgia, Cambria, 'Times New Roman', Times, serif);

$haas-text: ('BWHaasDingbat', 'BWHaasText', 'Helvetica Neue', Helvetica, Arial, sans-serif);
$haas-head: ('BWHaasDingbat', 'BWHaasHead', 'Helvetica Neue', Helvetica, Arial, sans-serif);

$icon-font: ('BWHaasDingbatPlus', 'BWHaasDingbat',  sans-serif);

$haas-mono-a: ('BWHaasDingbat', 'BWHaasTextMonoA', 'BWHaasText', Helvetica, Arial, sans-serif);
$haas-mono-b: ('BWHaasDingbat', 'BWHaasTextMonoB', 'BWHaasText', Helvetica, Arial, sans-serif);
$haas-mono-c: ('BWHaasDingbat', 'BWHaasTextMonoC', 'BWHaasText', Helvetica, Arial, sans-serif);

$serif-text: $publico-text !default;
$sans-text: $haas-text !default;
$serif-head: $publico-head !default;
$sans-head: $haas-head !default;
