@import '../settings/globals';
@import '../settings/colors';
@import '../tools/functions';
@import '../tools/mixins';

.dvz-content {
  quotes: '“' '”' '‘' '’';

  .quote,
  %quote {
    &::before {
      content: open-quote;
    }

    &::after {
      content: close-quote;
    }
  }

  .apostrophe {
    &::before {
      content: '’';
    }
  }

  .pullquote {
    margin: 30px auto;
    width: 75%;

    .pullquote-quote {
      @extend %quote;

      position: relative;
      margin: 18px 0 12px;
      line-height: 1.2;
      font-family: $haas-head;
      font-size: remify(40px);
      font-weight: 400;

      @include below($b-mobile-breakpoint) {
        font-size: remify(32px);
      }

      &::before {
        position: absolute;
        right: 100%;
      }
    }

    .pullquote-src {
      margin: 12px 0 0;
      line-height: 1.3;
      font-family: $sans-text;
      font-size: remify(14px);

      @include below($b-mobile-breakpoint) {
        font-size: remify(12px);
      }
    }
  }
}
