// sass-lint:disable zero-unit

@import './functions';

@mixin webfont($family, $file, $weight: 400, $style: normal) {
  $path: '/graphics/soup/fonts/';

  @font-face {
    font-family: $family;
    font-weight: $weight;
    font-style: $style;
    src: url('#{$path}#{$file}.woff2') format('woff2'), url('#{$path}#{$file}.woff') format('woff');
  }
}


////////////////////////////////////////
//|
//| Breakpoint media queries
//|
////////////////////////////////////////

@mixin below($width) {
  $_width: strip-unit($width) * 1px;

  @media (max-width: $_width) {
    @content;
  }
}


@mixin above($width) {
  $_width: strip-unit($width) * 1px;

  @media (min-width: $_width) {
    @content;
  }
}
