// Disable links on the page and remove the link style on body copy links

.dvz-t-show,
.dvz-t-show-inline {
  display: none;
}

.terminal {
  .dvz-content {
    .dvz-t-disable,
    a:not(.dvz-t-allow) {
      border: 0;
      cursor: auto;
      text-decoration: none;
      pointer-events: none;

      &:hover { // sass-lint:disable-line nesting-depth
        cursor: auto;
      }
    }

    .dvz-t-show {
      display: block;
    }

    .dvz-t-show-inline {
      display: inline-block;
    }

    .dvz-t-hide {
      display: none;
    }

    .dvz-t-remove {
      display: none;
    }
  }
}
