@import '../settings/colors';
@import '../tools/functions';

$lede-image-caption-align: center !default;
$lede-image-caption-font-family: $sans-text !default;
$lede-image-caption-font-size: 14px !default;
$lede-image-caption-color: inherit !default;
$lede-image-margin-top: -70px !default;

.lede-image {
  $height: 370px;

  background-color: $gray1;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: $height;
  overflow: hidden;

  .article-title {
    margin-top: $height * 0.4;
  }
}

.lede > .lede-image {
  margin-top: $lede-image-margin-top;
}

.lede-caption {
  margin-top: 7px;
  text-align: $lede-image-caption-align;
  line-height: 1.2;
  color: $lede-image-caption-color;
  font-family: $lede-image-caption-font-family;
  font-size: remify($lede-image-caption-font-size);

  @include below($b-mobile-breakpoint) {
    font-size: remify(12px);
  }
}
