@import '../settings/colors';
@import '../tools/functions';

$footer-margin-bottom: 0 !default;
$footer-border-top: 1px solid #dddddd !default;
$footer-padding: 8px 0 !default;
$footer-font-family: inherit !default;
$footer-color: $gray4 !default;
$footer-link-border-style: dotted !default;

.dvz-content {
  .footer {
    position: relative;
    margin-top: 20px;
    margin-bottom: $footer-margin-bottom;
    border-top: $footer-border-top;
    padding: $footer-padding;
    color: $footer-color;
    font-family: $footer-font-family;
    font-size: remify(13px);

    a[href] {  // sass-lint:disable-line no-qualifying-elements force-attribute-nesting
      border-bottom-style: $footer-link-border-style;
    }
  }

  .dvz-correction {
    line-height: 1.3;
    font-size: remify(14px);
    font-style: italic;

    @include below($b-mobile-breakpoint) {
      font-size: remify(13px);
    }
  }

  .caption,
  .source {
    color: $gray4;
    font-size: remify(13px);
  }
}
