// sass-lint:disable zero-unit brace-style

/// https://css-tricks.com/snippets/sass/strip-unit-function/
/// Remove the unit of a length
/// @param {Number} $n - Number to remove unit from
/// @return {Number} - Unitless number
@function strip-unit($n) {
  @if type-of($n) == 'number' and not unitless($n) {
    @return $n / ($n * 0 + 1);
  }
  @return $n;
}

/// Change from number or pixels to rems
/// @param {Number} $n - Number convert
/// @return {Number} - Number in rem units
@function remify($n) {
  @if unit($n) == 'px' {
    @return strip-unit($n) / 16 * 1rem;
  } @else {
    @return strip-unit($n) * 1rem;
  }
}
