@import '../settings/globals';
@import '../tools/mixins';

.dvz-content {
  $ai2html-not-loaded-gray: #f7f7f7;

  .ai2html {
    margin-top: 14px;

    img {
      pointer-events: none;
      user-select: none;
    }
  }

  @include below($copy-breakpoint) {
    .ai2html[data-responsiveness='fixed'] .dvz-artboard {
      margin-left: 0 !important; // sass-lint:disable-line no-important
    }
  }

  .dvz-artboard {
    position: relative;
    background-color: $ai2html-not-loaded-gray;

    &::after {
      display: block;
      position: absolute;
      top: 25%;
      transform: translate(0, -50%);
      width: 100%;
      text-align: center;
      font-size: 14px;
      content: 'Loading...';
    }

    * {
      opacity: 0 !important; // sass-lint:disable-line no-important
    }

    &.ll-loaded {
      background-color: transparent;

      &::after {
        display: none;
      }

      * {
        opacity: 1 !important; // sass-lint:disable-line no-important
      }
    }
  }

  @include below($copy-breakpoint) {
    .ai2html-center {
      .ai2html[data-responsiveness='fixed'] .dvz-artboard {
        margin-left: auto !important; // sass-lint:disable-line no-important
      }
    }
    .ai2html-right {
      .ai2html[data-responsiveness='fixed'] .dvz-artboard {
        margin-left: auto !important; // sass-lint:disable-line no-important
        margin-right: 0 !important; // sass-lint:disable-line no-important
      }
    }
  }
}
