@import '../settings/globals';
@import '../settings/colors';
@import '../tools/functions';

$chart-header-font-family: $sans-text !default;
$chart-title-line-height: 1.24 !default; // ~22px
$chart-title-font-size: 18px !default;
$chart-title-font-weight: 700 !default;
$chart-dek-margin: 0 !default;
$chart-dek-line-height: 1.24 !default; // ~21px
$chart-dek-font-size: 17px !default;
$chart-dek-font-weight: 400 !default;
$chart-source-margin-bottom: 28px !default;
$chart-source-line-height: 1.15 !default; // ~15px
$chart-source-font-size: 13px !default;
$chart-key-dash-height: 3px !default;

.dvz-content {
  .asset-block,
  .chart-block {
    margin: 21px auto;
  }

  .asset-title,
  .chart-title {
    line-height: $chart-title-line-height;
    font-family: $chart-header-font-family;
    font-size: remify($chart-title-font-size);
    font-weight: $chart-title-font-weight;

    @include below($b-mobile-breakpoint) {
      line-height: $chart-title-line-height * 1.02;
      font-size: remify($chart-title-font-size * 0.9);
    }

    &.asset-title-lite,
    &.chart-title-lite {
      font-size: remify(24px);
      font-weight: 400;

      @include below($b-mobile-breakpoint) {
        line-height: 1.2;
        font-size: remify(24px);
      }
    }
  }

  .asset-dek,
  .chart-dek {
    margin-top: $chart-dek-margin;
    line-height: $chart-dek-line-height;
    font-family: $chart-header-font-family;
    font-size: remify($chart-dek-font-size);
    font-weight: $chart-dek-font-weight;

    @include below($b-mobile-breakpoint) {
      line-height: $chart-dek-line-height * 1.02;
      font-size: remify($chart-dek-font-size * 0.9);
    }
  }

  .asset-key,
  .asset-block figcaption, // sass-lint:disable-line force-element-nesting
  .chart-key,
  .chart-source {
    margin: 0 auto;
    font-family: $sans-text;
    font-weight: 400;
  }

  .asset-block figcaption, // sass-lint:disable-line force-element-nesting
  .chart-source {
    margin-top: 21px;
    margin-bottom: $chart-source-margin-bottom;
    line-height: $chart-source-line-height;
    font-size: remify($chart-source-font-size);

    p {
      margin-bottom: 4px;
    }
  }

  .asset-key,
  .chart-key {
    margin-top: 7px;
    margin-bottom: 7px;
    list-style: none;
    font-size: remify(14px);

    .asset-key-item,
    .chart-key-item {
      display: inline-block;
      margin-right: 7px;
    }

    %chart-item {
      display: inline-block;
      margin-right: 3px;
      background-color: currentColor;
      color: $gray2;
    }

    .asset-key-line,
    .chart-key-line {
      @extend %chart-item;
      $dash-height: $chart-key-dash-height;

      transform: rotate(-45deg) translate($dash-height * 2 + 1, 0);
      width: 17px;
      height: $dash-height + 1;
    }

    .asset-key-square,
    .asset-key-circle,
    .chart-key-square,
    .chart-key-circle {
      @extend %chart-item;
      $symbol-size: 8px;

      border: 1px solid;
      width: $symbol-size;
      height: $symbol-size;
    }

    .asset-key-circle,
    .chart-key-circle {
      border-radius: 50%;
    }

    .asset-key-outline,
    .chart-key-outline {
      background: transparent;
    }
  }

  .asset-box,
  .chart-box {
    margin-top: 7px;
  }
}
