@import '../settings/globals';
@import '../settings/colors';
@import '../tools/functions';
@import '../tools/mixins';

$type-primary-line-height: 1.558 !default;
$type-primary-font-family: $sans-text !default;
$type-primary-head-font-family: $sans-head !default;
$type-primary-font-size: 18px !default;

$type-article-title-margin: 0 !default;
$type-article-title-line-height: 1.2 !default;
$type-article-title-line-height-tablet: 1.2 !default;
$type-article-title-line-height-mobile: 1.1 !default;
$type-article-title-font-size: 52px !default;
$type-article-title-font-size-tablet: 49px !default;
$type-article-title-font-size-mobile: 38px !default;
$type-article-title-font-weight: 700 !default;

$type-article-subtitle-line-height: 1.3 !default;
$type-article-subtitle-font-size: 24px !default;
$type-article-subtitle-font-size-tablet: 24px !default;
$type-article-subtitle-font-size-mobile: 21px !default;
$type-article-subtitle-font-weight: 500 !default;
$type-article-subtitle-margin: 49px !default;
$type-article-subtitle-margin-tablet: 0 !default;
$type-article-subtitle-margin-mobile: 32px !default;

$type-chapter-hed-margin-bottom: -0.4rem !default;
$type-chapter-hed-line-height: inherit !default;
$type-chapter-hed-font-weight: 700 !default;
$type-chapter-hed-font-size: 32px !default;
$type-chapter-hed-font-size-tablet: 32px !default;
$type-chapter-hed-font-size-mobile: 28px !default;

$type-section-hed-font-weight: 700 !default;
$type-section-hed-margin-top: 0 !default;
$type-section-hed-margin-bottom: -0.25rem !default;

$type-bydek-margin: 14px auto 56px !default;
$type-bydek-line-height: 1.35 !default;
$type-bydek-line-height-tablet: 1.35 !default;
$type-bydek-font-size: 18px !default;
$type-bydek-font-size-tablet: 18px !default;
$type-bydek-font-size-mobile: 15px !default;

$type-p-margin: 0.5rem auto 1.5rem !default;
$type-p-text-indent: 0 !default;

.dvz-icon {
  font-family: $icon-font;
}

.dvz-content {
  text-rendering: optimizeLegibility;
  line-height: $type-primary-line-height;
  color: $black;
  font-family: $type-primary-font-family;
  font-size: remify($type-primary-font-size);
  // sass-lint:disable-all
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smooth: always;
  // sass-lint:enable-all

  a[href], // sass-lint:disable-line no-qualifying-elements force-attribute-nesting
  a[href]:visited { // sass-lint:disable-line no-qualifying-elements force-attribute-nesting
    border-bottom: 1px dotted currentColor;
    text-decoration: none;
    color: inherit;

    &:hover,
    &:active {
      border-bottom-style: solid;
    }
  }

  .article-title {
    margin-bottom: $type-article-title-margin;
    text-align: center;
    line-height: $type-article-title-line-height;
    font-family: $type-primary-head-font-family;
    font-size: remify($type-article-title-font-size);
    font-weight: $type-article-title-font-weight;

    @include below($b-tablet-breakpoint) {
      line-height: $type-article-title-line-height-tablet;
      font-size: remify($type-article-title-font-size-tablet);
    }

    @include below($b-mobile-breakpoint) {
      line-height: $type-article-title-line-height-mobile;
      font-size: remify($type-article-title-font-size-mobile);
    }
  }

  .article-subtitle {
    margin-top: 7px;
    margin-bottom: $type-article-subtitle-margin;
    text-align: center;
    line-height: $type-article-subtitle-line-height;
    font-family: $type-primary-head-font-family;
    font-size: remify($type-article-subtitle-font-size);
    font-weight: $type-article-subtitle-font-weight;

    @include below($b-tablet-breakpoint) {
      margin-bottom: $type-article-subtitle-margin-tablet;
      font-size: remify($type-article-subtitle-font-size-tablet);
    }

    @include below($b-mobile-breakpoint) {
      margin-bottom: $type-article-subtitle-margin-mobile;
      font-size: remify($type-article-subtitle-font-size-mobile);
    }
  }

  .chapter-hed {
    margin-top: 2rem;
    margin-bottom: $type-chapter-hed-margin-bottom;
    line-height: $type-chapter-hed-line-height;
    font-family: $type-primary-font-family;
    font-size: remify($type-chapter-hed-font-size);
    font-weight: $type-chapter-hed-font-weight;

    @include below($b-tablet-breakpoint) {
      font-size: remify($type-chapter-hed-font-size-tablet);
    }

    @include below($b-mobile-breakpoint) {
      font-size: remify($type-chapter-hed-font-size-mobile);
    }
  }

  .section-hed {
    margin-top: $type-section-hed-margin-top;
    margin-bottom: $type-section-hed-margin-bottom;
    font-family: $type-primary-font-family;
    font-size: remify(21px);
    font-weight: $type-section-hed-font-weight;

    @include below($b-mobile-breakpoint) {
      font-size: remify(20px);
    }
  }

  .bydek {
    margin: $type-bydek-margin;
    text-align: center;
    line-height: $type-bydek-line-height;
    font-family: $sans-text;
    font-size: remify($type-bydek-font-size);

    @include below($b-tablet-breakpoint) {
      line-height: $type-bydek-line-height-tablet;
      font-size: remify($type-bydek-font-size-tablet);
    }

    @include below($b-mobile-breakpoint) {
      margin-bottom: 35px;
      font-size: remify($type-bydek-font-size-mobile);
    }

    a {
      transition: color 0.2s ease-out;
      border-bottom: 0;
      text-decoration: underline;

      &:hover,
      &:active {
        color: $gray4;
      }
    }
  }

  .lede-left {
    .article-title,
    .article-subtitle,
    .bydek,
    .lede-caption {
      text-align: left;
    }
  }

  .boldtext,
  %boldtext {
    font-family: $sans-text;
    font-weight: 700;
  }

  .first-line-bold::first-line {
    font-family: $sans-text;
    font-weight: 700;
  }

  .first-line-caps::first-line {
    letter-spacing: 2px;
    font-variant: small-caps;
  }

  .copy-block {
    font-family: $serif-text;

    .boldtext {
      @extend %boldtext
    }

    // sass-lint:disable-all
    p:not([class]),
    p.first-line-bold,
    p.first-line-caps,
    p.copy-width,
    p.container-width {
      // sass-lint:enable-all
      margin: $type-p-margin;
      text-indent: $type-p-text-indent;
    }

    .chapter-hed + p,
    .section-hed + p {
      margin-top: 0;
    }

    @include below($b-mobile-breakpoint) {
      font-size: remify(16px);
    }
  }

}

